/* FOR DESKTOP  */
input[type="checkbox"] {
  position: absolute;
  top: -9999px;
  left: -9999px;
}

#filter_main_container {
  padding: 25px;
}

#stocks-advance-filter-grid-main-container {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-areas:
    "first second third"
    "fourth fifth sixth";
  color: black;
  grid-gap: 15px;
}

#div_filter_main_container {
  display: none;
  background-color: #f5f5f5;
  margin: 30px 40px 0px 0px;
  border-radius: 8px;
  box-shadow: 1px 3px 3px 1px #cacaca;
}

input[type="checkbox"]:checked ~ #div_filter_main_container {
  display: block;
}

#filter_icon {
  color: #39abf7;
  font-size: 22px;
}

#advance-fliter-dividend {
  grid-area: first;
  padding: 10px 60px 10px 60px;
  background-color: #ffffff;
}
#advance-filter-market-cap {
  grid-area: second;
  padding: 10px 60px 10px 60px;
  background-color: #ffffff;
}
#advance-filter-share-price {
  grid-area: third;
  padding: 10px 60px 10px 60px;
  background-color: #ffffff;
}
#advance-filter-price-to-equity {
  grid-area: fourth;
  padding: 10px 60px 10px 60px;
  background-color: #ffffff;
}
#advance-filter-revenues {
  grid-area: fifth;
  padding: 10px 60px 10px 60px;
  background-color: #ffffff;
}
#advance-filter-net-profit {
  grid-area: sixth;
  padding: 10px 60px 10px 60px;
  background-color: #ffffff;
}

#advance-filter-button {
  color: #ffffff;
  background-color: #39abf7;
  border: 0px;
  border-radius: 2px;
  margin-top: 20px;
  padding: 7px 35px 7px 35px;
  cursor: pointer;
}

#advance-filter-button:hover {
  background-color: #2c3e50;
  padding: 7px 65px 7px 65px;
  transition: all ease-out 0.4s;
}

#advance-filter-debt-to-equity {
  grid-area: fifth;
  padding: 10px 60px 10px 60px;
  background-color: #ffffff;
}

#advance-filter-title {
  display: flex;
  color: black !important;
}

#advance-filter-p-dividend {
  font-size: 16px !important;
  margin: 5px 0px 15px 0px !important;
}
#advance-filter-dividend-p {
  font-size: 16px !important;
  margin: 5px 0px 15px 0px !important;
}
#advance-filter-market-cap-p {
  font-size: 16px !important;
  margin: 5px 0px 15px 0px !important;
}

#advance-filter-debt-to-equity-p {
  font-size: 16px !important;
  margin: 5px 0px 15px 0px !important;
}
#advance-filter-share-price-p {
  font-size: 16px !important;
  margin: 5px 0px 15px 0px !important;
}

#advance-filter-value {
  font-size: 14px !important;
  margin: 5px 0px !important;
  font-weight: 400 !important;
}
#advance-filter-value2 {
  font-size: 14px !important;
  margin: 0px !important;
  font-weight: 400 !important;
}
#advance-filter-price-to-equity-p {
  font-size: 16px !important;
  margin: 5px 0px 15px 0px !important;
}
#advance-filter-price-to-equity-p {
  font-size: 16px !important;
  margin: 5px 0px 15px 0px !important;
}

#advance-filter-debt-to-equity-ratio-value {
  display: flex;
  justify-content: space-between;
}

#advance-label {
  cursor: pointer;
}

#close_filter {
  font-size: 24px;
  color: #2c3e50;
  cursor: pointer;
  transition: all ease-out 0.2s;
  margin: 10px 0px 0px 1440px;
}

#close_filter:hover {
  font-size: 26px;
  color: #39abf7;
}

#more_filters_title {
  color: #2c3e50;
}

#more_filters_title:hover {
  color: #39abf7;
}

@media only screen and (max-width: 426px) {
  #stocks-advance-filter-grid-main-container {
    grid-template-columns: 1fr;
    grid-template-areas:
      "first" "second" "third"
      "fourth" "fifth" "sixth";
    grid-gap: 10px;
  }
  #advance-label {
    display: block;
  }
  #div_filter_main_container {
    margin: 10px 15px 10px 10px;
  }
  #filter_main_container {
    padding: 10px 11px 10px 10px;
  }
  #advance-fliter-dividend {
    padding: 10px 15px 10px 15px;
  }
  #advance-filter-market-cap {
    padding: 10px 15px 10px 15px;
  }
  #advance-filter-share-price {
    padding: 10px 15px 10px 15px;
  }
  #advance-filter-price-to-equity {
    padding: 10px 15px 10px 15px;
  }
  #advance-filter-revenues {
    padding: 10px 15px 10px 15px;
  }
  #advance-filter-net-profit {
    padding: 10px 15px 10px 15px;
  }
  #close_filter {
    font-size: 22px;
    margin: 5px 0px 0px 250px;
  }
  #close_filter:hover {
    font-size: 24px;
  }
}
