  cursor: pointer;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
  background-color: #39abf7;
  color: #fff;
  border: none;
  text-decoration: none;
  outline: none;
  margin-top: 100px;
}

.buyStocksBox {
  margin: 0 auto;
