}

.buy {
  font-size: 1em;
  border-radius: 20px;
  padding: 10px 20px;
  color: #fff;
  background-color: #2c3e50;
  text-decoration: none;
  cursor: pointer;
  -webkit-transition: all 0.3s ease-out;
  transition: all 0.3s ease-out;
}
.buy:hover {
  background: #39abf7;
}

.buyStocksoverlay {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.7);
  -webkit-transition: opacity 500ms;
  transition: opacity 500ms;
  visibility: hidden;
  opacity: 0;
}
.buyStocksoverlay:target {
  visibility: visible;
  opacity: 1;
}

.buyStockspopup {
  margin: 150px auto;
  padding: 8px;
  background: #2c3e50;
  border-radius: 5px;
  width: 95%;
  position: relative;
  -webkit-transition: all 5s ease-in-out;
  transition: all 5s ease-in-out;
}

.buyStockspopup h2 {
  margin-top: 0;
  color: #fff;
  font-family: Tahoma, Arial, sans-serif;
  font-weight: 500;
  font-size: 40px;
}
.buyStockspopup .buyStocksclose {
  position: absolute;
  top: 20px;
  right: 30px;
  -webkit-transition: all 200ms;
  transition: all 200ms;
  font-size: 30px;

  font-weight: bold;
  text-decoration: none;
  color: #fff;
}
.buyStockspopup .buyStocksclose:hover {
  color: #39abf7;
  cursor: pointer;
}
.buyStockspopup .buyStockscontent {
  max-height: 30%;
  /* overflow: auto; */
  color: #fff;
}

.buyStockspopup .buyStockscontent p {
  font-size: 18px;
  padding: 10px;
}

