/* FOR MOBILE  */

#analysis_main_container {
  background-color: transparent;
}

#analysis_main_container_blur {
  filter: blur(15px);
  -webkit-filter: blur(15px);
  pointer-events: none;
}

#bg-text {
  color: #2c3e50;
  font-weight: bold;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  margin-top: 80px;
  text-align: center;
  font-size: 18px;
}

#bg-no-text {
  display: none;
}

#button_for_blur {
  color: #ffffff;
  background-color: #39abf7;
  padding: 6px 20px 6px 20px;
  border: none;
  border-radius: 4px;
  margin-top: 18px;
  font-size: 16px;
  cursor: pointer;
}

#button_for_blur:hover {
  background-color: #2c3e50;
}

#compare_analysis {
  margin: 10px 0px 0px 0px;
  display: flex;
  text-align: center;
}
#compare_analysis p {
  margin: -5px 15px 0px 0px;
  font-size: 14px;
  color: #2c3e50;
}
#company-analysis-similar-cimpany-dropdown {
  padding: 20px 50px 20px 50px;
}
#analysis-stock-chart {
  padding: 20px 50px 20px 50px;
}
#analysis_graph_title {
  text-align: center;
  font-size: 20px;
}
#analysis-price-summary {
  background-color: #cacaca;
  padding: 20px 50px 20px 50px;
}

/* FOR RECOMMENDATION  */

#analysis-recommendation-grid-container {
  display: grid;
  grid-template-areas: "company1" "company2";
  grid-template-columns: 1fr 1fr;
  padding: 20px 50px 20px 50px;
}
#analysis-recommendation-1 {
  grid-area: company1;
  text-align: center;
}

#analysis-recommendation-2 {
  grid-area: company2;
  text-align: center;
}

/* FOR FINANCIAL POSITIONAL GRAPH  */

#analysis-financial-positional-grid-container {
  display: grid;
  grid-template-areas:
    "financial-graph-1"
    "financial-graph-2";
}

#analysis-financial-positional-1 {
  grid-area: "financial-graph-1";
  text-align: center;
}
#analysis-financial-positional-2 {
  grid-area: "financial-graph-2";
  text-align: center;
}
#analysis-gauge-1 {
  width: 250px;
}
#analysis-gauge-2 {
  width: 250px;
}

#analysis-financial-positional-1 {
  display: none;
}

#analysis-financial-positional-2 {
  display: none;
}

#company_analaysis_table {
  padding: 15px 0px 0px 0px;
  text-align: center;
  box-shadow: 0px 4px 6px 0px #cacaca;
}

/* FOR DESKTOP  */
@media screen and (min-width: 480px) {
  #analysis-recommendation-grid-container {
    display: grid;
    grid-template-areas: "company1 company2";
    grid-template-columns: 1fr 1fr;
    padding: 20px 50px 20px 50px;
  }
  #analysis-recommendation-1 {
    grid-area: company1;
  }

  #analysis-recommendation-2 {
    grid-area: company2;
  }

  /* FOR FINANCIAL POSITION GRAPH 
   */
  #analysis-financial-positional-grid-container {
    display: grid;
    grid-template-areas: "financial-graph-1  financial-graph-2";
    padding: 20px 50px 20px 50px;
  }

  #analysis-financial-positional-1 {
    grid-area: "financial-graph-1";
  }
  #analysis-financial-positional-2 {
    grid-area: "financial-graph-2";
  }
  #analysis-gauge-1 {
    width: 500px;
  }
  #analysis-gauge-2 {
    width: 500px;
  }
  #analysis-financial-positional-1 {
    display: contents;
  }
  #analysis-financial-positional-2 {
    display: contents;
  }
  #compare_analysis p {
    margin: 5px 15px 0px 0px;
    font-size: 18px;
  }
  #compare_analysis {
    text-align: left;
  }
  #company_analaysis_table {
    margin: auto;
    padding: 20px 0px 0px 0px;
    text-align: center;
    width: 92%;
  }
}
